import React, { Fragment, useEffect, useState } from "react";
import { Formik, ErrorMessage, useFormik } from "formik";
import * as Yup from "yup";
import axios from "axios";

import {
  Label,
  Error,
  TextInput,
  InputName,
  FormGroup,
  FormGroupControl,
  FormGroupPrepend,
  FormGroupAppend,
  CurrencySelect,
  ButtonPrimary
} from "components/common";

import {
  Heading,
  SubHeading,
  Form,
  FormName,
  ExchangeRate,
  Rate,
  Divider,
  TotalFees,
  TotalToPay,
  Fee
} from "./styles";

export function SendRequest({ setStep }) {
  const formik = useFormik({
    initialValues: {
      email: "",
      send: 0,
      recipient: 0,
      sendCurrency: {
        value: "usd",
        label: "USD"
      },
      recipientCurrency: {
        value: "brl",
        label: "BRL"
      }
    },
    onSubmit: values => {
      alert(JSON.stringify(values, null, 2));
    }
  });

  const [coins, setCoins] = useState([]);

  useEffect(() => {
    async function getCoins() {
      await axios
        .get("https://api.exchangeratesapi.io/latest")
        .then(response => {
          const keys = Object.keys(response.data.rates);

          const options = keys.map(key => {
            return {
              value: key.toLowerCase(),
              label: key.toUpperCase()
            };
          });

          setCoins(options);
        });
    }

    getCoins();
  }, []);

  async function convert() {
    await axios
      .get(
        `https://api.exchangeratesapi.io/latest?base=${formik.values.sendCurrency.label}`
      )
      .then(response => {
        const baseValue =
          response.data.rates[formik.values.recipientCurrency.label];

        const send = formik.values.send;

        formik.setValues({ ...formik.values, recipient: baseValue * send });
      });
  }

  useEffect(() => {
    if (formik.values.sendCurrency) {
      convert();
    }
  }, [
    formik.values.send,
    formik.values.sendCurrency.value,
    formik.values.recipientCurrency.value
  ]);

  return (
    <Fragment>
      <Heading>Send Money</Heading>
      <SubHeading>
        Send your money anytime, anywhere around the globe.
      </SubHeading>

      <Formik
        initialValues={formik.values}
        validationSchema={() =>
          Yup.object().shape({
            email: Yup.string()
              .email()
              .required("A recipient email address is required"),
            send: Yup.number()
              .min(10, "A minimum amount of $10 is required")
              .required("A send amount is required")
          })
        }
        onSubmit={(values, { setSubmitting }) => {
          setStep({ step: "confirm", details: { ...values } });
          setSubmitting(false);
        }}
      >
        <Form onSubmit={formik.handleSubmit}>
          <FormName>Personal Details</FormName>

          <Label label="email">
            <InputName>Recipient</InputName>
            <TextInput
              id="email"
              type="email"
              name="email"
              onChange={formik.handleChange}
              onBlur={formik.onBlur}
              value={formik.values.email}
              placeholder="Enter Email Address"
            />

            <ErrorMessage component={Error} name="email" />
          </Label>

          <Label label="send" nomargin>
            <InputName>You Send</InputName>
            <FormGroup>
              <FormGroupPrepend>$</FormGroupPrepend>
              <FormGroupControl
                id="send"
                type="number"
                step="0.01"
                min="10"
                name="send"
                onChange={formik.handleChange}
                onBlur={formik.onBlur}
                value={formik.values.send}
              />

              <FormGroupAppend>
                <Label
                  label="currency"
                  aria-label="select the currency you would like to send"
                  nomargin
                >
                  <CurrencySelect
                    options={coins}
                    defaultValue={formik.values.sendCurrency}
                    onChange={formik.setFieldValue}
                    value={formik.values.sendCurrency}
                    name="sendCurrency"
                    isSearchable
                  />
                </Label>
              </FormGroupAppend>
            </FormGroup>
            <ErrorMessage component={Error} name="send" />
          </Label>

          <Label
            label="recipient"
            aria-label="select the currency you would like the recipient to receive"
            nomargin
          >
            <InputName>Recipient Gets</InputName>
            <FormGroup>
              <FormGroupPrepend>$</FormGroupPrepend>
              <FormGroupControl
                id="recipient"
                type="number"
                name="recipient"
                value={formik.values.recipient.toFixed(2)}
                readOnly
              />
              <FormGroupAppend>
                <Label label="currency" nomargin>
                  <CurrencySelect
                    options={coins}
                    name="recipientCurrency"
                    defaultValue={formik.values.recipientCurrency}
                    value={formik.values.recipientCurrency}
                    onChange={formik.setFieldValue}
                    isSearchable
                  />
                </Label>
              </FormGroupAppend>
            </FormGroup>
          </Label>

          <ExchangeRate>
            The current exchange rate is <Rate></Rate>
          </ExchangeRate>

          <Divider />

          <TotalFees>
            Total fees
            <Fee>
              {formik.values.send && formik.values.send.toFixed(2)}{" "}
              {formik.values.sendCurrency.label}
            </Fee>
          </TotalFees>
          <TotalToPay>
            Total To Pay
            <Fee>
              {formik.values.recipient && formik.values.recipient.toFixed(2)}{" "}
              {formik.values.recipientCurrency.label}
            </Fee>
          </TotalToPay>

          <ButtonPrimary stretch type="submit">
            Continue
          </ButtonPrimary>
        </Form>
      </Formik>
    </Fragment>
  );
}
