import React, { useEffect } from "react"
import { Link, navigate } from "@reach/router"
import { Formik, ErrorMessage } from "formik"
import * as Yup from "yup"
import axios from "axios"
import { Wrapper } from "./styles"
import {
  Form,
  FormName,
  Error,
  Label,
  TextInput,
  InputName,
  Account,
  ButtonLink,
  ButtonPrimary
} from "components/common"

function Signup() {
  return (
    <Wrapper>
      <Formik
        initialValues={{
          name: "",
          email: "",
          phone: "",
          password: "",
          conf_password: "",
          submitError: false
        }}
        validationSchema={() =>
          Yup.object().shape({
            name: Yup.string()
              .trim()
              .matches(
                /[\w\s]$/,
                "Name can only contain letters, and cannot include any special characters, punctionations or start with a space"
              )
              .required("Full name is required"),
            phone: Yup.string()
            .matches(
              /[\w\s]$/,
              "Name can only contain letters, and cannot include any special characters, punctionations or start with a space"
            )
            .required("Phone is required"),
            email: Yup.string()
              .email()
              .required("Email address is required"),
            password: Yup.string()
  
              .required("Password is required"),

            conf_password: Yup.string()
            
            .required("is Password is required")
          })
        }
        onSubmit={async (
          { name, email, phone, password, conf_password },
          { setSubmitting, setFieldValue }
        ) => {
          try {
            const user = await axios.post("https://cambio-api.herokuapp.com/users", {
              name,
              email,
              phone,
              password,
              conf_password
            })

            navigate("/login")
        
          } catch (err) {
            setSubmitting(false)
            setFieldValue("submitError", true)
          }
        }}
      >
        {({
          values,
          handleChange,
          handleBlur,
          handleSubmit
        }) => (
          <Form onSubmit={handleSubmit}>
            <FormName>Sign Up</FormName>

            <Label label="name">
              <InputName>Full Name</InputName>
              <TextInput
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.name}
                type="text"
                name="name"
                placeholder="NOME COMPLETO"
              />
              <ErrorMessage component={Error} name="name" />
            </Label>

            <Label label="email">
              <InputName>Email Address</InputName>
              <TextInput
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.email}
                type="email"
                name="email"
                placeholder="E-MAIL"
              />
              <ErrorMessage component={Error} name="email" />
            </Label>


            <Label label="phone">
              <InputName>Phone</InputName>
              <TextInput
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.phone}
                type="cell"
                name="phone"
                placeholder="PHONE"
              />
              <ErrorMessage component={Error} name="phone" />
            </Label>

            <Label label="password">
              <InputName>Password</InputName>
              <TextInput
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.password}
                type="password"
                name="password"
                placeholder="SENHA"
              />
              <ErrorMessage component={Error} name="password" />
            </Label>


            <Label label="conf_password">
              <InputName>Password</InputName>
              <TextInput
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.conf_password}
                type="password"
                name="conf_password"
                placeholder="CONFIRME SUA SENHA"
              />
              <ErrorMessage component={Error} name="conf_password" />
            </Label>

            <ButtonPrimary stretch margin="0.5rem 0" type="submit">
              Sign Up
            </ButtonPrimary>
          </Form>
        )}
      </Formik>

      <Account>
        Already have an account?{" "}
        <ButtonLink  style={{color: "#013689"}} as={Link} to="/login">
          Log In
        </ButtonLink>
      </Account>
    </Wrapper>
  )
}

export default Signup
